<template>
    <!-- qoute qoute--audio-player -->
    <div style="margin-bottom: 70px;" v-if="item.file">
        <AudioPlayer :sources="sources" :classList="addClass" />
    </div>
</template>

<script>
    import AudioPlayer from '@/components/AudioPlayer';
    import Story from "../../views/dashboard/Story";

    export default {
        name: "AudioStory",
        props: [
            'addClass', 'item'
        ],
        components: {
            Story,
            AudioPlayer
        },
        data() {
            return {
                sources: []
            }
        },
        async mounted() {
            if(this.item.file) {
                this.sources.push({
                    file: this.item.file.url,
                    type: null
                });
            }
        }
    }
</script>

<style>
    .audio-player {
        height: 61px;
    }
</style>