                <img class="header-screen-hamburger" src="~@/assets/img/icon/hamburger.svg" alt=""
                     @click.prevent="showHamburger = true">
                <div class="fixed-header__box">
                    <div class="fixed-header__link-wrapper">
                        <span class="fixed-header__link fixed-header__link--bigger">{{ memorial.name }}</span>
                        <router-link :to="{ name: 'home' }" class="fixed-header__link-sub">
                            <svg xmlns="http://www.w3.org/2000/svg" width="9.792" height="15.857"
                                 viewBox="0 0 9.792 15.857">
                                <path id="Path_1413" data-name="Path 1413"
