                                      fill="currentColor"/>
                            </svg>
                            {{ $ml.get('memorial.header.toHome') }}
                        </router-link>
                    </div>
                    <router-link :to="{ name: 'memorials.life', params: { slug: this.memorial.slug } }"
                                 class="fixed-header__link fixed-header__life-story fixed-header__link--margin active"
                    >{{ $ml.get('memorial.header.life') }}
                    </router-link>
                    <router-link :to="{ name: 'memorials.relatives', params: { slug: this.memorial.slug } }"
                                 class="fixed-header__link"
                    >{{ $ml.get('memorial.header.relatives') }}
                    </router-link>
                </div>
                <div class="fixed-header__logo">
                    <router-link :to="{ name: 'home' }">
                        <img class="card__logo" src="~@/assets/img/logo.svg" alt="Logo">
                    </router-link>
                </div>
            </div>
        </div>

