            <div class="life-main__container">

            <!-- photos-block -->
                <div class="life-main__photo-block photos-block">
                    <div class="photos-block__img photos-block__img--1" :class="{'photos-block__without-image': !getOnePeriodPhoto(0)}">
                        <div class="life-period__mask"></div>
                        <img class="photos-block__img-1" :src="getOnePeriodPhoto(0)" alt="">
                        <img class="photos-block__img-2" src="~@/assets/img/icon/life-baseline.svg" alt="">
                    </div>

                    <div class="photos-block__img photos-block__img--2" :class="{'photos-block__without-image': !getMemorialPhoto()}">
                        <div class="life-period__mask"></div>
                        <img class="photos-block__img-1" :src="getMemorialPhoto()" alt="">
                        <img class="photos-block__img-2" src="~@/assets/img/icon/life-face.svg" alt="">
                    </div>

                    <div class="photos-block__img photos-block__img--3" :class="{'photos-block__without-image': !getOnePeriodPhoto(1)}">
                        <div class="life-period__mask"></div>
                        <img class="photos-block__img-1" :src="getOnePeriodPhoto(1)" alt="">
                        <img class="photos-block__img-2" src="~@/assets/img/icon/life-picture.svg" alt="">
                    </div>

                    <div class="photos-block__img photos-block__img--4" :class="{'photos-block__without-image': !getOnePeriodPhoto(2)}">
                        <div class="life-period__mask"></div>
                        <img class="photos-block__img-1" :src="getOnePeriodPhoto(2)" alt="">
                        <img class="photos-block__img-2" src="~@/assets/img/icon/life-videocam.svg" alt="">
                    </div>
                </div>
                <div class="life-main__title-wrapper" v-if="periods !== undefined">
                    <h1 class="life-main__title first-header">{{ $ml.get('life.periods.title') }}</h1>
                    <div class="life-main__box">
                        <div class="life-main__scale">{{ $ml.get('life.periods.desc') }}</div>
                        <div class="life-main__decor"></div>
                        <div class="life-main__years">{{ getTimelineRange || '1964 - 1997' }}</div>
                    </div>

                    <div class="placeholder" v-if="!periods.length">
                        <ul class="placeholder__points">
                            <li class="placeholder__point">
                                <div class="placeholder__descr placeholder__point-1"></div>
                                <span class="placeholder__date"></span>
                            </li>
                            <li class="placeholder__point ">
                                <div class="placeholder__descr placeholder__point-2"></div>
                                <span class="placeholder__date"></span>
                            </li>
                            <li class="placeholder__point">
                                <div class="placeholder__descr placeholder__point-3"></div>
                                <span class="placeholder__date"></span>
                            </li>
                            <li class="placeholder__point ">
                                <div class="placeholder__descr placeholder__point-4"></div>
                                <span class="placeholder__date"></span>
                            </li>
                            <li class="placeholder__point">
                                <div class="placeholder__descr placeholder__point-5"></div>
                                <span class="placeholder__date"></span>
                            </li>
                            <li class="placeholder__point ">
                                <div class="placeholder__descr placeholder__point-6"></div>
                                <span class="placeholder__date"></span>
                            </li>
                        </ul>

                        <div class="placeholder__for-memory">
                            <div class="placeholder__text placeholder__text-1"></div>
                            <div class="placeholder__text placeholder__text-2"></div>
                        </div>
                    </div>

                    <ul class="life-main__life-points" v-if="periodsWithYears && periodsWithYears.length">
                        <li class="life-main__point"
                            v-for="period in periodsWithYears"
                            :key="period.id"
                            :href="`#${period.id}`"
                            v-smooth-scroll
                        >
                            <div class="life-main__descr">{{ period.name }}</div>
                            <span class="life-main__date">{{ period.since | getYear }} - {{ period.till | getYear }}</span>
                        </li>
                    </ul>

                    <div class="life-main__for-memory" v-if="periodsWithoutYears && periodsWithoutYears.length">
                        <div class="life-main__text"
                             v-for="period in periodsWithoutYears"
                             :key="period.id"
                             :href="`#${period.id}`"
                             v-smooth-scroll
                        >{{ period.name }}
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <div class="smoothPicker-wrapper" v-if="years && years.length">
            <smooth-picker ref="smoothPicker" :data="pickerData" :change="dataChange"/>
        </div>

        <section class="life-period" id="periods" v-if="memorial && periods.length">
            <!-- life-main__container life-period__container -->
            <div class="life-main__container life-period__container"
                 v-for="(period, pk) in periods" :key="period.id" :id="period.id" :ref="`period-${pk}`"
                 v-waypoint="{ active: true, callback: onWaypoint, options: { threshold: [0] } }"
            >
                <!-- life-period__order -->
                <div :class="['life-period__order', {'life-period__order--padding': !isMobile}, {'life-period__order--mod': isMobile}]">
                    <div class="life-period__title second-header"><span>{{ period.name }}</span></div>
                    <div class="life-period__date" v-if="period.since">{{ period.since | getYear }} - {{ period.till | getYear }}</div>
                    <router-link v-if="$store.getters['auth/isLoggedIn'](['customer'])"
                                 :tag="'button'"
                                 :to="{ name: 'memorials.story.create', params: { slug: memorial.id, period: period.id } }" type="button"
                                 class="life-period__btn">
                        <img src="~@/assets/img/icon/button-white-plus.svg" alt="">{{ $ml.get('life.buttons.period.createStory') }}
                    </router-link>
                    <router-link v-if="!$store.getters['auth/isLoggedIn'](['customer'])"
                                 :tag="'button'"
                                 :to="{ name: 'memorials.story.auth', params: { slug: memorial.slug, period: period.id } }" type="button"
                                 class="life-period__btn">
                        <img src="~@/assets/img/icon/button-white-plus.svg" alt="">{{ $ml.get('life.buttons.period.createStory') }}
                    </router-link>
                </div>

                <!-- life-period__section -->
                <div class="life-period__section life-period__section--padding">

                    <div class="life-period__section-inner">
                        <div class="life-period__decor-bold" v-if="pk > 0"></div>

                        <div v-for="(story, key) in period.all" :key="story.id">
                        <div class="life-period__user-wrapper">
                            <!-- life-period__user -->
                            <div class="life-period__user">
                                <div class="life-period__user-img">
                                    <img :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                                         :alt="story.owner.user.name">
                                </div>
                                <div class="life-period__box">
                                    <div class="life-period__user-name">{{ story.owner.user.name }}</div>
                                    <div class="life-period__user-relation">{{ story.owner.relationToMemorialPerson }}
                                    </div>
                                </div>
                            </div>

                            <div class="life-period__text" v-html="story.description">
                            </div>
                        </div>

                        <div v-for="(item, key) in story.content">
                            <ImageStory v-if="item.contentType == 'image'" :addClass="'life-period__first-photo'"
                                        :item="item" :key="'image-'+item.id"/>
                            <ImageStory v-else-if="item.type == 'link'" :addClass="'life-period__first-photo'"
                                        :item="item" :key="'image-'+item.id"/>
                            <AudioStory v-else-if="item.contentType == 'audio'" :addClass="['post__qoute-audio']" :item="item" :key="'audio-'+item.id"/>
                            <VideoStory v-else-if="item.contentType == 'video'" :item="item" :key="'video-'+item.id"/>
                        </div>
                        <div v-if="key != (period.all.length - 1)" class="life-period__decor"></div>
                    </div>
                    </div>
                </div>
            </div>
        </section>

        <HelpCenter
          v-if="memorial"
          :withStoryBtn="true"
          :memorialSlug="$store.getters['auth/isLoggedIn'](['customer']) ? memorial.id : memorial.slug"
        />

        <section class="life-period" v-if="memorial && !periods.length">
            <div class="life-main__container life-period__container">
                <!-- life-period__order -->
                <div class="life-period__order life-period__order--padding">

                    <div class="third-placeholder">
                        <div class="third-placeholder__item third-placeholder__item-1"></div>
                        <div class="third-placeholder__item third-placeholder__item-2"></div>
                    </div>
                    <button type="button" class="life-period__btn"><img src="~@/assets/img/icon/button-white-plus.svg"
                                                                        alt="">
                        {{ $ml.get('life.buttons.period.createStory') }}
                    </button>

                </div>

                <!-- life-period__section -->
                <div class="life-period__section life-period__section--padding">
                    <div class="second-placeholder">
                        <div class="second-placeholder__wrapper">
                            <div class="second-placeholder__user">
                                <div class="second-placeholder__user-img">
                                </div>
                                <div class="second-placeholder__box">
                                    <div class="second-placeholder__user-name"></div>
                                    <div class="second-placeholder__user-relation"></div>
                                </div>
                            </div>

                            <div class="second-placeholder__item second-placeholder__item-1"></div>
                            <div class="second-placeholder__item second-placeholder__item-2"></div>
                            <div class="second-placeholder__item second-placeholder__item-3"></div>
                        </div>
                        <div class="second-placeholder__photo">
                        </div>
                        <div class="second-placeholder__second-photo">
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <section class="page-footer">
            <div class="container">
                <div class="page-footer__title-wrapper">
                    <div class="page-footer__logo">
                        <img src="~@/assets/img/icon/footer-logo.svg" alt="">
                    </div>
                    <p class="page-footer__title-text">
                        {{ $ml.get('memorial.footer.text') }}
                    </p>
                </div>

                <div class="page-footer__bottom">
                    <div class="page-footer__privacy-box">
                        <a :href="`./files/${$ml.get(`footer.link1File`)}`"><img
                                src="~@/assets/img/icon/footer-flag.svg" alt="">{{ $ml.get(`memorial.footer.link1`)
                            }}</a>
                        <a :href="`./files/${$ml.get(`footer.link2File`)}`">{{ $ml.get(`memorial.footer.link2`) }}</a>
                        <a href="#">{{ $ml.get(`memorial.footer.link3`) }}</a>
                    </div>

                    <div class="page-footer__btn-box">
                        <a href="https://eternity.co.il/about-us" target="_blank" class="page-footer__btn">{{
                            $ml.get('memorial.footer.button1') }}</a>
                        <a href="" class="page-footer__btn" @click.prevent.stop="openHelp">{{ $ml.get('memorial.footer.button2') }}</a>
                    </div>

                    <div class="page-footer__rights">
                        {{ $ml.get('memorial.footer.copy') }}
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import Vue from 'vue';
    import {mapGetters} from "vuex";
    import VueWaypoint from 'vue-waypoint'

    import HelpCenter from '@/components/HelpCenter.vue';
    import ImageStory from "@/components/storiesFull/ImageStory";
    import AudioStory from "@/components/storiesFull/AudioStory";
    import VideoStory from "@/components/storiesFull/VideoStory";
    import SmoothPicker from '@/components/vue-smooth-picker'
    import '@/components/vue-smooth-picker/dist/css/style.css'

    import { EventBus } from '@/utils/EventBus.js';

    // Waypoint plugin
    Vue.use(VueWaypoint)
    Vue.use(SmoothPicker)

    export default {
        name: 'Life',
        components: {
            ImageStory,
            AudioStory,
            VideoStory,
            HelpCenter
        },
        data() {
            return {
                showHamburger: false,
                stickyHeader: false,
                pickerData: [{
                    currentIndex: 1,
                    flex: 2,
                    list: this.years,
                    textAlign: 'center',
                    onClick: this.clickOnGender,
                }]
            }
        },
        computed: {
            ...mapGetters({
                token: 'auth/token',
                memorial: 'story/memorial',
                periods: 'period/getPeriodsFiltered',
                periodsWithYears: 'period/getPeriodsWithYears',
                years: 'period/getYears',
                periodsWithoutYears: 'period/getPeriodsWithoutYears',
                getTimelineRange: 'period/getTimelineRange',
                isMemorialExist: 'story/isMemorialExist',
            }),
          isMobile() {
            return window.innerWidth < 768;
          },
        },
        methods: {
            onWaypoint ({ el, going, direction }) {
                // going: in, out
                // direction: top, right, bottom, left
                let id = el.getAttribute('id');
                // console.log(`#${el.getAttribute('id')} is ${going} viewport, direction: ${direction}`)
                el.classList.toggle('active', this.$waypointMap.GOING_IN === going)

                if (going === this.$waypointMap.GOING_IN) {
                    // console.log('waypoint going in!')

                    for(let i in this.periods) {
                        if(this.periods[i].id == id) {
                            this.$refs.smoothPicker.setCurrentIndexList(i);
                            break;
                        }
                    }
                }

                // if (direction === this.$waypointMap.DIRECTION_TOP) {
                //     console.log('waypoint going top!')
                // }
            },
            openHelp() {
              EventBus.$emit('help-center-open');
            },
            clickOnGender() {
                const iIndex = this.$refs.smoothPicker.getCurrentIndexList()
                const myEl = document.getElementById(this.periods[iIndex].id);

                this.$smoothScroll({
                    scrollTo: myEl,
                })
                // console.info('Clicked index:' + iIndex)
            },
            dataChange(gIndex, iIndex) {
                // console.info('list', gIndex, iIndex)

                const myEl = document.getElementById(this.periods[iIndex].id);

                this.$smoothScroll({
                    scrollTo: myEl,
                })
            },
            handleScroll(event) {
                this.stickyHeader = window.scrollY > 100;
            },
            getMemorialPhoto() {
                return this.memorial.profilePhoto ? this.memorial.profilePhoto.url : null;
            },
            getOnePeriodPhoto(index) {
                if (this.periods && this.periods.length && this.periods[index] !== undefined) {
                    let period = this.periods[index];

                    if(period.thumbnail) {
                        return period.thumbnail.url;
                    } else {
                        if(period.defaultImageName) {
                            return require(`@/assets/img/icon/periods/${period.defaultImageName}/1x.png`);
                        }
                    }
                }

                return null;
            },
        },
        beforeMount() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        async created() {
            await this.$store.commit('story/setMemorialExist', false);
            await this.$store.commit('story/setMemorial', null);
            await this.$store.commit('period/setPeriods', []);

            if (!this.token) {
                await this.$store.dispatch('auth/getToken');
            }

            await this.$store.dispatch('story/getMemorial', this.$route.params.slug);

            if (!this.isMemorialExist) {
                await this.$router.push({name: 'home'});
            }
            else {
                await this.$store.dispatch('period/getPeriods', {id: this.memorial.id, storyType: 'all', withGeneral: false});
                this.pickerData[0].list = this.years;
            }

            if(this.$route.params.period) {
                const myEl = document.getElementById(this.$route.params.period);

                this.$smoothScroll({
                    scrollTo: myEl,
                })
            }
        }
    }
</script>

<style lang="scss">
    @import "~@/assets/scss/pages/life-story.scss";

    .smooth-picker {
        cursor: pointer;
    }
</style>